
import { defineComponent, SetupContext } from 'vue';
import BaseBtn from '../atoms/BaseBtn.vue';

export default defineComponent({
  name: 'StartBtn',
  components: {
    'base-btn': BaseBtn
  },
  setup(props, context: SetupContext) {
    const startClick = (): void => {
      context.emit('start-click');
    };

    return { startClick };
  }
});
